import {
  addHiddenFinalProp,
  setPrototypeOf,
  set, get, del, extend,
} from './obx/utils';
import { getObx, ObxFlag } from './obx/observable/obx';
import { asObservable } from './obx/observable/observable';

export interface IScope {
  readonly $super?: IScope;
  $get(key: string): any;
  $set(key: string, val: any): void;
  $del(key: string): void;
  $extend(properties: object): void;
  $derive(data: object): IScope;
  [property: string]: any;
}

export class BaseScope implements IScope {
  $get(key: string) {
    return get(this, key);
  }

  $set(key: string, val: any) {
    set(this, key, val);
  }

  $del(key: string) {
    del(this, key);
  }

  $extend(properties: object) {
    extend(this, properties);
  }

  $derive(data: object): IScope {
    return asScope(data, this);
  }

  $(key: string): any {
    return this.$get(key);
  }
}

export function asScope<T extends object>(data: T, parentScope: IScope | null = null): T & IScope {
  // FIXME: use parent obxFlag
  asObservable(data, ObxFlag.DEEP);

  if (parentScope) {
    const a = getObx(data);
    const b = getObx(parentScope);
    (a as any).parent = b;
  }

  addHiddenFinalProp(data, '$super', parentScope);
  setPrototypeOf(data, parentScope || new BaseScope());

  return (data as any);
}



