interface Fn<I extends unknown[], R> {
  (...input: I): R;
}

interface Mockable<I extends unknown[], R> extends Fn<I, R> {
  mock?: Fn<I, R>;
}

export function createApi<I extends unknown[], R = any>(fn: Fn<I, R>) {
  const f: Mockable<I, R> = (...input) => {
    if (f.mock) {
      return f.mock(...input);
    }
    return fn(...input);
  };

  return f;
}



