import ObservableProperty from './obx/observable/obx-property';
import { IArea } from './area';
import { IScope } from './scope';
import ObservableView from './observable-view';
import { resetDerivationState } from './obx/derivation';
import { ObxFlag } from './obx/observable/obx';

export type PropGetter = (scope: IScope, area: IArea) => any;

export interface PropConfig {
  key: string;
  expr?: PropGetter;
  value?: any;
  spread?: boolean;
}

export default class Prop {
  key: string;

  private spread: boolean = false;
  private data: any;
  private reactiveData: ObservableProperty | null = null;

  constructor(private area: IArea, config: PropConfig, view?: ObservableView) {
    this.key = config.key;

    if (config.expr) {
      this.reactiveData = new ObservableProperty(
        config.key,
        area.scope,
        config.expr,
        undefined,
        config.value,
        [ area, view ],
        ObxFlag.REF
      );
    } else {
      this.data = config.value;
    }

    this.spread = Boolean(config.spread);
  }

  getData(): any {
    if (this.reactiveData) {
      if (this.reactiveData.scope !== this.area.scope) {
        this.reactiveData.scope = this.area.scope;
        resetDerivationState(this.reactiveData);
      }

      return this.reactiveData.get();
    } else {
      return this.data;
    }
  }

  isSpread() {
    return this.spread;
  }
}



