import { render } from 'react-dom';
import { ReactType, ReactElement, ReactNode, createElement, Fragment } from 'react';
import { hasOwnProperty, looseEqual, isObject, objectAssign } from '../core/obx/utils';
import XView from './x';
import Link from './router/link';
import NavLink from './router/nav-link';
import Route from './router/route';
import Redirect from './router/redirect';
import navigator, { HistoryOptions } from './navigator';
import Root from './router/root';

function iterMap(data: Map<any, any>, fn: (item: any, key: any) => void) {
  data.forEach(fn);
}
function iterSet(data: Set<any>, fn: (item: any, key: any) => void) {
  let index = 0;
  data.forEach((item) => {
    fn(item, String(index++));
  });
}

export function XFor(data: any, delegate: (key: string, val: any) => any) {
  if (Array.isArray(data)) {
    return data.map((item, index) => delegate(String(index), item));
  }

  if (data) {
    if ((data instanceof Set) || (data instanceof Map)) {
      const frags: any[] = [];

      const fn = (item: any, key: any): void => {
        frags.push(delegate(key, item));
      };
      data instanceof Map ? iterMap(data, fn) : iterSet(data, fn);

      return frags;
    }

    return Object.keys(data).map((key) => {
      return delegate(key, (data as any)[key]);
    });
  }

  return null;
}

export function X(area: any, render: (area: any) => ReactNode) {
  if (area.isVirtual) {
    return render(area);
  }

  return createElement(XView, {
    key: area.key,
    area,
    children: render,
  });
}

export function xId(obj: any, defaultKey: any): string {
  return String(obj && obj.$id || defaultKey);
}

function isNativeEvent(e: any) {
  if (e && e.nativeEvent && e.target) {
    return true;
  }
  return false;
}

export function xAssign(setter: (v: any) => void, getter: () => any, data: any) {
  if (!isNativeEvent(data)) {
    setter(data);
    return;
  }

  const target = data.target;
  if (target.nodeName === 'INPUT') {
    if (target.type === 'radio') {
      if (target.checked) {
        setter(target.value);
      }
    } else if (target.type === 'checkbox') {
      const data = getter();
      if (Array.isArray(data)) {
        if (target.checked) {
          data.push(target.value);
        } else {
          let l = data.length;
          while (l-- >= 0) {
            if (looseEqual(data[l], target.value)) {
              data.splice(l, 1);
            }
          }
        }
        setter(data);
      } else {
        setter(target.checked);
      }
    } else {
      setter(target.value);
    }
  } else if (target.nodeName === 'SELECT') {
    const data = Array.prototype.filter.call(target.options, (o: any) => o.selected)
      .map((o: any) => o.value);
    setter(target.multiple ? data : data[0]);
  } else {
    setter(target.value);
  }
}

export interface ComponentsMap {
  [key: string]: ReactType;
}

export interface VT {
  (type: ReactType, props?: any, children?: ReactNode[]): ReactElement<any>;
  get: (type: ReactType) => ReactType;
  register: (typeOrMaps: string | ComponentsMap, Component?: ReactType) => void;
  wrapperWith: (maps: ComponentsMap) => VT;
}

function notFound(type: string) {
  return () => createElement('div', null, `Component "${type}" Not Found.`);
}

function getComponent(maps: ComponentsMap, name: string) {
  const ns = name.split('.');
  let key = ns.shift()!;
  if (!hasOwnProperty(maps, key)) {
    return null;
  }

  let component = maps[key];
  while (ns.length > 0) {
    key = ns.shift()!;
    component = (component as any)[key];
    if (!component) {
      return notFound(name);
    }
  }
  return component;
}

function create(type: ReactType, props: any, children?: ReactNode[]) {
  if (!children || children.length < 1) {
    return createElement(type, props);
  }

  return createElement(type, props, ...children);
}

export const V: VT = function (type: ReactType, props?: any, children?: ReactNode[]): ReactElement<any> {
  return create(V.get(type), props, children);
} as VT;

function register(maps: ComponentsMap, typeOrMaps: string | ComponentsMap, Component?: ReactType) {
  if (typeof typeOrMaps === 'string' && Component) {
    maps[typeOrMaps] = Component;
  } else if (isObject(typeOrMaps)) {
    Object.keys(typeOrMaps).forEach((key) => {
      maps[key] = typeOrMaps[key];
    });
  }
}

function wrapperWith(P: VT, maps: ComponentsMap): VT {
  const R: any = function (type: ReactType, props?: any, children?: ReactNode[]) {
    return create(R.get(type), props, children);
  };

  R.get = (type: ReactType): ReactType => {
    if (typeof type === 'string') {
      const temp = getComponent(maps, type);
      if (temp) {
        return temp;
      }
    }

    return P.get(type);
  };
  R.wrapperWith = (xmaps: ComponentsMap) => wrapperWith(R, xmaps);
  R.register = (type: string, Component: ReactType) => register(maps, type, Component);

  return R as VT;
}

const globalMaps: ComponentsMap = {};
const internalMaps: ComponentsMap = {
  Fragment,
  Link,
  NavLink,
  Route,
  Redirect,
};

V.get = (type: ReactType): ReactType => {
  if (typeof type === 'string') {
    const temp = getComponent(globalMaps, type);
    if (temp) {
      return temp;
    }

    if (hasOwnProperty(internalMaps, type)) {
      return internalMaps[type];
    }
  }

  return type;
};
V.register = (typeOrMaps: string | ComponentsMap, Component?: ReactType) => register(globalMaps, typeOrMaps, Component);
V.wrapperWith = (maps: ComponentsMap) => wrapperWith(V, maps);

interface Modifiers {
  [modifier: string]: (e: KeyboardEvent) => false | void;
}

const ModifiersMap: Modifiers = {
  stop(e: Event) {
    e.stopPropagation();
  },
  prevent(e: Event) {
    e.preventDefault();
  },
  enter(e: KeyboardEvent): false | void {
    if (e.keyCode !== 13) {
      return false;
    }
  },
  ctrl(e: KeyboardEvent): false | void {
    if (!e.ctrlKey) {
      return false;
    }
  },
  alt(e: KeyboardEvent): false | void {
    if (!e.altKey) {
      return false;
    }
  },
  shift(e: KeyboardEvent): false | void {
    if (!e.shiftKey) {
      return false;
    }
  },
  meta(e: KeyboardEvent): false | void {
    if (!e.metaKey) {
      return false;
    }
  },
  tab(e: KeyboardEvent): false | void {
    if (e.keyCode !== 9) {
      return false;
    }
  },
  delete(e: KeyboardEvent): false | void {
    // 'Backspace', 'Delete'
    if (e.keyCode !== 8 && e.keyCode !== 46) {
      return false;
    }
  },
  esc(e: KeyboardEvent): false | void {
    if (e.keyCode !== 27) {
      return false;
    }
  },
  space(e: KeyboardEvent): false | void {
    if (e.keyCode !== 32) {
      return false;
    }
  },
  up(e: KeyboardEvent): false | void {
    if (e.keyCode !== 38) {
      return false;
    }
  },
  down(e: KeyboardEvent): false | void {
    if (e.keyCode !== 40) {
      return false;
    }
  },
  left(e: KeyboardEvent): false | void {
    if (e.keyCode !== 37) {
      return false;
    }
  },
  right(e: KeyboardEvent): false | void {
    if (e.keyCode !== 39) {
      return false;
    }
  }
};

export function xModifiers(modifiers: string[]) {
  const modifierQueue = modifiers.map((modifier) => ModifiersMap[modifier]).filter(Boolean);

  return (_: any, e: any): false | void => {
    if (e && e.nativeEvent) {
      const brk = modifierQueue.some((fn) => fn(e) === false);
      if (brk) {
        return false;
      }
    }
  };
}

export interface AppConfig {
  history?: HistoryOptions;
  globalComponents?: ComponentsMap;
  globalHelpers?: { [key: string]: any };
  containerId?: string;
}

export const globalHelpers: { [key: string]: any } = {};

export function reportError(e: Error | string) {
  // TODO refactor
  const { AliMonitor } = global as any;

  if (AliMonitor) {
    AliMonitor.reportError(typeof e === 'string' ? new Error(e) : e);
  }
}

export function runApp(AppComponent: any, config: AppConfig = {}): void {
  // init history
  navigator.init(config.history);

  if (config.globalComponents) {
    register(globalMaps, config.globalComponents);
  }

  if (config.globalHelpers) {
    objectAssign(globalHelpers, config.globalHelpers);
  }

  const containerId = config.containerId || 'app';

  let container = document.getElementById(containerId);
  if (!container) {
    container = document.createElement('div');
    document.body.appendChild(container);
    container.id = containerId;
  }
  render(createElement(
    Root, null,
    (props: any) => createElement(AppComponent as ReactType, {
      ...props,
      key: 'appRoot',
    }),
  ), container);
}



