import { Component, createElement as h } from 'react';
import { reportError } from './utils';

export interface XState {
  revision: number;
  error: any;
}

export interface XProps {
  area: any;
  children: (area: any) => any;
}

function renderError(error: any) {
  if (error === true) {
    return null;
  }
  if (process.env.NODE_ENV !== 'production') {
    return h('pre', { style: {
      border: '1px solid #ffa39e',
      backgroundColor: '#fff1f0',
      padding: '8px 15px',
    } }, `${error.message}: ${error.stack}`);
  } else {
    reportError(error)
  }
  return '<Error>';
}

export default class X extends Component<XProps, XState> {
  static displayName = 'X';

  static getDerivedStateFromProps(props: XProps, state: XState): XState | null {
    const { area } = props;
    if (area.isDirty() || state.revision < 0) {
      let revision = state.revision;
      let error = null;
      try {
        revision = area.getRevision();
      } catch (e) {
        error = true;
      }
      return {
        revision,
        error,
      };
    }

    return null;
  }

  state = { revision: -1, error: null };

  private dispose: () => void;

  constructor(props: XProps) {
    super(props);

    const { area } = this.props;
    this.dispose = area.observe(() => this.setState({
      revision: area.getRevision(),
      error: null,
    }));
  }

  componentWillUnmount() {
    if (this.dispose) {
      this.dispose();
    }
  }

  componentDidCatch(err: any, info: any) {
    const message = err.message || String(err);
    const stack = info && info.componentStack || '';
    const error = { message, stack };
    const { area } = this.props;
    try {
      area.scope.$didCatch(error, area);
    } catch (e) {}
    this.setState({ error });
  }

  shouldComponentUpdate(_: any, nextState: XState) {
    return nextState.revision !== this.state.revision || nextState.error !== null;
  }

  render() {
    if (this.state.error) {
      return renderError(this.state.error);
    }

    return this.props.children(this.props.area);
  }
}



