import {
  hasOwnProperty,
  walk,
  invariant,
  addHiddenFinalProp,
  nextId,
  splitPath,
} from '../utils';
import { defineObxProperty } from './obx-property';
import { IObservable, propagateChanged, startBatch, endBatch } from './observable';
import { IDerivation, DerivationState, clearObserving } from '../derivation';

export enum ObxFlag {
  REF = 0,
  SHALLOW = 1,
  DEEP = 2,
}

export function formatNestValue(nestPath: string, val: any): any {
  if (!nestPath) {
    return val;
  }

  const pathArray = splitPath(nestPath, true);

  if (!pathArray) {
    return val;
  }

  const [ _, path, key ] = pathArray;

  return formatNestValue(path, key ? { [key]: val } : val);
}

class Obx<T = any[] | object> implements IObservable, IDerivation {
  id = nextId();
  observing: IObservable[] = [];
  observers = new Set();
  dependenciesState = DerivationState.NOT_TRACKING;
  lowestObserverState = DerivationState.UP_TO_DATE;

  constructor(public name: string, public target: T, public obxFlag: ObxFlag = ObxFlag.DEEP) {

  }

  onBecomeDirty() {
    propagateChanged(this);
  }

  onBecomeUnobserved() {
    clearObserving(this);
  }

  onBecomeObserved() {

  }

  reportChange() {
    startBatch();
    propagateChanged(this);
    endBatch();
  }

  getAsObx(path: PropertyKey): Obx<T | any[] | object> | void {
    if (path === '') {
      return this;
    }

    let entry = path;
    let nestPath: string = '';

    if (typeof path !== 'symbol') {
      const pathArray = splitPath(String(path));

      if (!pathArray) {
        return this;
      }

      entry = pathArray[1];
      nestPath = pathArray[2];
    }

    if (!entry) {
      return this.get(nestPath);
    }

    let ret = this.internalGet(entry);

    if (!hasObx(ret) && nestPath) {
      ret = this.internalGet(path);
    }

    const obx = getObx(ret);

    if (obx && nestPath) {
      return obx.getAsObx(nestPath);
    }

    return obx;
  }

  internalGet(key: PropertyKey): any {
    return (this.target as any)[key];
  }

  get(path?: PropertyKey): any {
    if (path == null || path === '') {
      return this.target;
    }

    let entry = path;
    let nestPath = '';

    if (typeof path !== 'symbol') {
      const pathArray = splitPath(String(path));

      if (!pathArray) {
        return undefined;
      }

      entry = pathArray[1];
      nestPath = pathArray[2];
    }

    if (!entry) {
      return this.get(nestPath);
    }

    const ret = this.internalGet(entry);

    if (!nestPath) {
      return ret;
    }

    if (!hasObx(ret)) {
      return this.internalGet(path);
    }

    return getObx(ret)!.get(nestPath);
  }

  internalSet(key: PropertyKey, val: any, nestPath: string = ''): void {
    if (this.obxFlag > ObxFlag.REF) {
      defineObxProperty(
        this.target as any, key,
        formatNestValue(nestPath, val),
        undefined, this.obxFlag === ObxFlag.DEEP ? ObxFlag.DEEP : ObxFlag.REF
      );
    } else {
      (this.target as any)[key] = formatNestValue(nestPath, val);
    }

    this.reportChange();
  }

  set(path: PropertyKey, val: any): void {
    if (path === '') {
      return;
    }

    let entry = path;
    let nestPath: string = '';

    if (typeof path !== 'symbol') {
      const pathArray = splitPath(String(path));

      if (!pathArray) {
        return;
      }

      entry = pathArray[1];
      nestPath = pathArray[2];
    }

    if (!entry) {
      if (nestPath) {
        this.set(nestPath, val);
      }
      return;
    }

    this.internalSet(entry, val, nestPath);
  }

  internalDel(key: PropertyKey) {
    if (!hasOwnProperty(this.target, key)) {
      return;
    }

    delete (this.target as any)[key];
    this.reportChange();
  }

  del(path: PropertyKey) {
    if (!path) {
      return;
    }

    let entry = path;
    let nestPath: string = '';

    if (typeof path !== 'symbol') {
      const pathArray = splitPath(String(path), true);

      if (!pathArray) {
        return;
      }

      entry = pathArray[1];
      nestPath = pathArray[2];
    }

    if (!entry) {
      this.del(nestPath);
      return;
    }

    if (nestPath) {
      const obx = this.getAsObx(nestPath);
      if (obx) {
        obx.del(entry);
      }
      return;
    }

    this.internalDel(entry);
  }

  extend(properties: object) {
    startBatch();
    walk(properties, (_, key, val) => this.set(key, val));
    endBatch();
  }
}

export default Obx;

export const SYMBOL_OBX = Symbol('__obx');
export function injectObx(obj: any[] | object, obx: Obx): void {
  addHiddenFinalProp(obj, SYMBOL_OBX, obx);
}

export function getObx(obj: any): Obx | undefined {
  return obj ? (obj as any)[SYMBOL_OBX] : undefined;
}

export function hasObx(obj: any[] | object): boolean {
  return hasOwnProperty(obj, SYMBOL_OBX) && (obj as any)[SYMBOL_OBX] instanceof Obx;
}



