const languageMap: { [key: string]: string } = {
  en: 'en_US',
	zh: 'zh_CN',
	zt: 'zh_TW',
	es: 'es_ES',
	pt: 'pt_PT',
	fr: 'fr_FR',
	de: 'de_DE',
	it: 'it_IT',
	ru: 'ru_RU',
	ja: 'ja_JP',
	ko: 'ko_KR',
	ar: 'ar_SA',
	tr: 'tr_TR',
	th: 'th_TH',
	vi: 'vi_VN',
	nl: 'nl_NL',
	he: 'iw_IL',
	id: 'in_ID',
	pl: 'pl_PL',
	hi: 'hi_IN',
	uk: 'uk_UA',
	ms: 'ms_MY',
  tl: 'tl_PH',
};

let localeCached: string | null = null;
export function setLocale(locale: string) {
  localeCached = locale;
}

export function getLocale(): string {
  if (localeCached) {
    return localeCached;
  }

  const { g_config, navigator } = global as any;
  if (g_config) {
    if (g_config.locale) {
      localeCached = languageMap[g_config.locale] || (g_config.locale as string);
      return localeCached;
    }
  }

  localeCached = (navigator.language as string).replace('-', '_') || 'zh_CN';

  return localeCached;
}

export function createI18n(key: string, locale?: string): (k: string) => string {
  if (locale) {
    setLocale(locale);
  } else {
    locale = getLocale();
  }

  let i18n: any;
  if ((global as any)[key]) {
    i18n = (global as any)[key][locale] || {};
  } else {
    key = `${key}_${locale.replace('_', '-').toLocaleLowerCase()}`;
    i18n = (global as any)[key] || {};
  }

  return (key: string): string => {
    return i18n[key];
  };
}



