import { globalState } from './global-state';
import { hasObx, injectObx, getObx } from './observable/obx';
import ObxInstance from './observable/obx-instance';

export function isObject(value: any): value is object {
  return value !== null && typeof value === 'object';
}

export function isPlainObject(value: any) {
  if (!isObject(value)) {
    return false;
  }
  const proto = Object.getPrototypeOf(value);
  return proto === Object.prototype || proto === null;
}

export function setPrototypeOf(target: any, proto: any) {
  // tslint:disable-next-line
  if (typeof Object.setPrototypeOf !== 'undefined') {
    Object.setPrototypeOf(target, proto);// tslint:disable-line
  } else {
    target.__proto__ = proto;
  }
}

export function isValidArrayIndex(val: any): boolean {
  const n = parseFloat(String(val));
  return n >= 0 && Math.floor(n) === n && isFinite(val);
}

export function nextId() {
  return (++globalState.guid).toString(32).toLocaleLowerCase();
}

const prototypeHasOwnProperty = Object.prototype.hasOwnProperty;
export function hasOwnProperty(obj: any, key: string | number | symbol): boolean {
  return obj && prototypeHasOwnProperty.call(obj, key);
}

export function objectAssign<T extends object>(target: T, ...sources: object[]): T {
  if (Object.assign) {
    return Object.assign(target, ...sources);
  }
  for (let i = 0, l = sources.length; i < l; i++) {
    const source = sources[i];
    for (const key in source) {
      if (hasOwnProperty(source, key)) {
        (target as any)[key] = (source as any)[key];
      }
    }
  }
  return target;
}

export interface Lambda {
  (): void;
  name?: string;
}

export function once(func: Lambda): Lambda {
  let invoked = false;
  return function(this: any) {
    if (invoked) {
      return;
    }
    invoked = true;
    return (func as any).apply(this, arguments);
  };
}

export function fail(message: string, thing?: any): never {
  invariant(false, message, thing);
  throw 'X'; // tslint:disable-line
}

export function invariant(check: boolean, message: string, thing?: any) {
  if (!check) {
    throw new Error('[recore] Invariant failed: ' + message + (thing ? ` in '${thing}'` : ''));
  }
}

export function walk(obj: object, fn: (obj: object, key: string, val: any) => void): void {
  const keys = Object.keys(obj) as any;
  for (let i = 0; i < keys.length; i++) {
    fn(obj, keys[i], (obj as any)[keys[i]]);
  }
}

export function addHiddenProp(object: any, propName: string | symbol, value: any) {
  Object.defineProperty(object, propName, {
    enumerable: false,
    writable: true,
    configurable: true,
    value,
  });
}

export function addHiddenFinalProp(object: any, propName: string | symbol, value: any) {
  Object.defineProperty(object, propName, {
    enumerable: false,
    writable: false,
    configurable: true,
    value,
  });
}

export function looseEqual(a: any, b: any): boolean {
  if (a === b) {
    return true;
  }
  if (!isObject(a) && !isObject(b)) {
    return String(a) === String(b);
  }
  return false;
}

export function looseIndexOf(arr: any[], val: any): number {
  for (let i = 0, l = arr.length; i < l; i++) {
    if (looseEqual(arr[i], val)) return i;
  }
  return -1;
}

const RE_PATH = /^([^/]*)(?:\/(.*))?$/;
const RE_PATH_REVERSE = /^(?:(.*)\/)?([^/])$/;
export function splitPath(path: string, reverse: boolean = false) {
  return reverse ? RE_PATH_REVERSE.exec(path) : RE_PATH.exec(path);
}

export interface DecoratorDescription {
  prop: string;
  descriptor?: PropertyDescriptor;
  decoratorTarget: any;
}

export const SYMBOL_DECORATORS = Symbol('__obxDecorators');

export interface DecoratorTarget {
  [SYMBOL_DECORATORS]?: { [prop: string]: DecoratorDescription };
}

export function isDecoratorTarget(a: any): a is DecoratorTarget {
  return a[SYMBOL_DECORATORS] ? true : false;
}

export function getObxDecorators(a: DecoratorTarget) {
  return a[SYMBOL_DECORATORS];
}

const descriptorCache: { [prop: string]: PropertyDescriptor } = {};

export function createPropertyInitializerDescriptor(prop: string): PropertyDescriptor {
  return (descriptorCache[prop] || (descriptorCache[prop] = {
    configurable: true,
    enumerable: false,
    get() {
      initializeInstance(this as DecoratorTarget);
      // TODO not safe
      return (this as any)[prop];
    },
    set(value) {
      initializeInstance(this as DecoratorTarget);
      // TODO not safe
      (this as any)[prop] = value;
    },
  }));
}

export const SYMBOL_INITIALIZED = Symbol('__obxInitialized');

export function initializeInstance(target: DecoratorTarget) {
  if ((target as any)[SYMBOL_INITIALIZED] === true) {
    return;
  }
  addHiddenProp(target, SYMBOL_INITIALIZED, true);

  if (!hasObx(target)) {
    const name = (target.constructor.name || 'ObservableObject') + '@' + nextId();
    const obx = new ObxInstance(name, target);
    injectObx(target, obx);
  }
}

export function get(target: any, key: PropertyKey): any {
  const obx = getObx(target);
  if (obx) {
    return obx.get(key);
  }
}

export function set(target: any, key: PropertyKey, val: any) {
  const obx = getObx(target);
  if (obx) {
    obx.set(key, val);
  }
}

export function del(target: any, key: PropertyKey) {
  const obx = getObx(target);
  if (obx) {
    obx.del(key);
  }
}

export function extend(target: any, properties: object) {
  const obx = getObx(target);
  if (obx) {
    obx.extend(properties);
  }
}

const useRAF = typeof requestAnimationFrame === 'function';

export function throttle(func: Function, wait: number) {
  let lastArgs: any;
  let lastThis: any;
  let result: any;
  let timerId: number | undefined;
  let lastCalled: number | undefined;
  let lastInvoked: number = 0;

  function invoke(time: number) {
    const args = lastArgs;
    const thisArg = lastThis;

    lastArgs = undefined;
    lastThis = undefined;
    lastInvoked = time;
    result = func.apply(thisArg, args);
    return result;
  }

  function startTimer(pendingFunc: any, wait: number): number {
    if (useRAF) {
      return requestAnimationFrame(pendingFunc);
    }
    return setTimeout(pendingFunc, wait) as any;
  }

  function leadingEdge(time: number) {
    lastInvoked = time;
    timerId = startTimer(timerExpired, wait);
    return invoke(time);
  }

  function shouldInvoke(time: number) {
    const timeSinceLastCalled = time - lastCalled!;
    const timeSinceLastInvoked = time - lastInvoked;

    return (
      lastCalled === undefined || (timeSinceLastCalled >= wait)
      || (timeSinceLastCalled < 0) || timeSinceLastInvoked >= wait
    );
  }

  function remainingWait(time: number) {
    const timeSinceLastCalled = time - lastCalled!;
    const timeSinceLastInvoked = time - lastInvoked;

    return Math.min(wait - timeSinceLastCalled, wait - timeSinceLastInvoked);
  }

  function timerExpired() {
    const time = Date.now();
    if (shouldInvoke(time)) {
      return trailingEdge(time);
    }

    timerId = startTimer(timerExpired, remainingWait(time));
  }

  function trailingEdge(time: number) {
    timerId = undefined;

    if (lastArgs) {
      return invoke(time);
    }

    lastArgs = undefined;
    lastThis = undefined;
    return result;
  }

  function debounced(this: any, ...args: any[]) {
    const time = Date.now();
    const isInvoking = shouldInvoke(time);

    lastArgs = args;
    lastThis = this;
    lastCalled = time;

    if (isInvoking) {
      if (timerId === undefined) {
        return leadingEdge(lastCalled);
      }

      timerId = startTimer(timerExpired, wait);
      return invoke(lastCalled);
    }

    if (timerId === undefined) {
      timerId = startTimer(timerExpired, wait);
    }

    return result;
  }

  return debounced;
}



