import { ReactNode, Component, createElement } from 'react';
import { V } from './utils';
import ViewController from '../core/view-controller';
import { hasOwnProperty, invariant } from '../core/obx/utils';
import { MatchResult } from './router/utils';
import navigator from './navigator';
import { shallowEqual } from '../core/utils';

export type RenderType = (c: any) => ReactNode;
export type RenderFactory = (V: any) => RenderType;

export interface PageRequest {
  uri: string;
  path: string;
  query: { [key: string]: any };
  params: { [key: string]: any };
  state: any;
}

function decode(s: string): string {
  if (s) {
    s = s.replace(/\+/g, '%20');
    s = decodeURIComponent(s);
  }
  return s;
}

function parseQuery(search: string): object {
  const params: any = {};

  if (!search) {
    return params;
  }

  if (search.indexOf('?') === 0) {
    search = search.substring(1);
  }

  const ps = search.split(/[&;]/);
  let p;
  let n;
  let k;
  let v;

  for (let i = 0, l = ps.length; i < l; i++) {
    p = ps[i];
    n = p.indexOf('=');

    if (n === 0) {
      continue;
    }
    if (n < 0) {
      k = p;
      v = null;
    } else {
      k = decode(p.substring(0, n));
      v = decode(p.substring(n + 1));
    }

    if (hasOwnProperty(params, k)) {
      if (!Array.isArray(params[k])) {
        params[k] = [params[k]];
      }
      params[k].push(v);
    } else {
      params[k] = v;
    }
  }
  return params;
}

export default function compose(renderFactory: RenderFactory, ControllerType?: any, routerView?: any) {
  let lazyRender: RenderType | null = null;

  if (!ControllerType) {
    ControllerType = class extends ViewController {
      $enter() {
        this.$extend(this.$props); // TODO should be here?
      }
    }
  }

  const proto = ControllerType.prototype;

  invariant(
    proto instanceof ViewController,
    `Controller ${ControllerType.name} must be extends "ViewController"`
  );

  if (routerView) {
    Object.defineProperty(proto, '$routerView', {
      configurable: false,
      enumerable: false,
      get() {
        console.warn('$routerView is deprecated, use <RouterView /> instead.');
        return routerView!(this);
      },
    });
  }

  Object.defineProperty(proto, '__routerView', {
    configurable: false,
    enumerable: false,
    value(props?: any) {
      return routerView ? routerView(this, props) : null;
    },
  });

  function createController(parent: any, props: object) {
    let _V = V;
    if (!lazyRender) {
      const { components } = ControllerType;
      if (components) {
        _V = V.wrapperWith(components);
      }
      lazyRender = renderFactory(_V);
    }

    const controller = new ControllerType(props);
    controller.$parent = parent;
    controller.__vxHelpers = ControllerType.helpers;
    controller.__V = _V;
    return controller;
  }

  function compileRequest(props: any, state: any) {
    let { controller } = state;
    const { match, location, parentController, defined, ...extras } = props;
    if (match instanceof MatchResult) {
      const loc = navigator.history!.location;
      const uri = loc.pathname + loc.search;

      if (!controller
        || state.uri !== uri
        || !shallowEqual(state.state, loc.state)
        || !shallowEqual(state.extras, extras))
      {
        const nextState: any = {
          uri,
          defined,
          state: loc.state,
          extras,
        };

        const request = {
          ...nextState,
          path: loc.pathname,
          params: match.params,
          query: parseQuery(loc.search),
        };

        if (!controller) {
          controller = createController(parentController, request);
          controller.$enter(true);
          nextState.controller = controller;
        } else {
          controller.$set('$props', request);
          controller.$enter();
        }

        return nextState;
      }
    } else if (!controller) {
      controller = createController(parentController, extras);
      controller.$enter(true);
      return {
        controller,
      };
    } else {
      controller.$set('$props', extras);
      controller.$enter();
    }

    return null;
  }

  class View extends Component<any> {
    static getDerivedStateFromProps(props: any, state: any) {
      return compileRequest(props, state);
    }

    static displayName = ControllerType.name || 'View';

    state: any = {};

    componentWillUnmount() {
      if (this.state.controller) {
        this.state.controller.$destroy();
      }
    }

    shouldComponentUpdate() {
      return false;
    }

    componentDidMount() {
      if (this.state.controller) {
        this.state.controller.$didMount();
      }
    }

    render() {
      if (!this.state.controller) {
        return null;
      }

      return lazyRender!(this.state.controller);
    }
  }

  return View;
}



