if (process.env.NODE_ENV !== 'production') {
  console.log(`%cRecore %cv${version} %cdevelopment`, 'color:#000;font-weight:bold;', 'color:green;font-weight:bold;', 'color:orange;font-weight:bold;');
} else {
  console.log(`%cRecore %cv${version} `, 'color:#000;font-weight:bold;', 'color:green;font-weight:bold;');
}

export * from './core';
export * from './lib';
export { version };



