import { BaseScope } from './scope';
import Area, { AreaConfig, IArea } from './area';
import { Lambda, initializeInstance, hasOwnProperty } from './obx/utils';
import { defineObxProperty } from './obx/observable/obx-property';
import { globalHelpers, reportError } from '../lib';
import { ReactInstance } from 'react';
import { ObxFlag } from './obx/observable/obx';

export interface ActionConfig {
  name: string;
  fn?: Lambda;
  params?: {};
}

export type FlowAction = string | ActionConfig | Lambda;

export interface PageRequest {
  uri: string;
  path: string;
  query: { [key: string]: any };
  params: { [key: string]: any };
  state: any;
}

export interface ComponentProps {
  [key: string]: any;
}

export default abstract class ViewController extends BaseScope {
  public $parent: ViewController | undefined;
  public $props: PageRequest | ComponentProps = {};
  public $refs: { [key: string]: ReactInstance } = {};
  private __area: Area | null = null;

  constructor(props: PageRequest | ComponentProps) {
    super();
    defineObxProperty(this, '$props', props, undefined, ObxFlag.REF);
    defineObxProperty(this, '$refs', {}, undefined, ObxFlag.SHALLOW);
  }

  __m(config: AreaConfig): Area {
    if (!this.__area) {
      this.__area = new Area(this, config);
    }

    return this.__area;
  }

  $init(params: PageRequest | ComponentProps) {
    // , times: 1
  }

  $receive(params: PageRequest | ComponentProps) {
    // times: 0+
  }

  $enter(firstEnter: boolean) {
    //  + , times: 1+
    if (firstEnter) {
      this.$init(this.$props);
    } else {
      this.$receive(this.$props);
    }
  }

  $didMount() {
    //  times: 1
  }

  $didCatch(e: Error) {
    //  times: 0+
  }

  $destroy() {
    //  times: 1
  }

  $action(actions: FlowAction | FlowAction[], area: IArea): () => void {
    if (!Array.isArray(actions)) {
      actions = [ actions ];
    }

    const actionList = parseActions(actions, this);

    return (...args: any[]) => {
      return doAction(actionList, this, { scope: area.scope }, args);
    };
  }

  $(key: string): any {
    const ret = this.$get(key);
    if (ret !== undefined) {
      if (typeof ret === 'function') {
        return ret.bind(this);
      }
      return ret;
    }

    const helpers = (this as any).__vxHelpers;
    if (hasOwnProperty(helpers, key)) {
      return helpers[key];
    }

    if (hasOwnProperty(globalHelpers, key)) {
      return globalHelpers[key];
    }

    if (global) {
      return (global as any)[key];
    }
  }

  $get(key: string) {
    initializeInstance(this as any);
    return super.$get(key);
  }

  $set(key: string, val: any) {
    initializeInstance(this as any);
    return super.$set(key, val);
  }

  $del(key: string) {
    initializeInstance(this as any);
    return super.$del(key);
  }

  $extend(properties: object) {
    initializeInstance(this as any);
    return super.$extend(properties);
  }

  $derive(data: object) {
    initializeInstance(this as any);
    return super.$derive(data);
  }
}

function parseActions(actions: FlowAction[], context: any) {
  return actions.map((action: any) => {
    if (!action) {
      return null;
    }

    if (Array.isArray(action)) {
      const subActions = parseActions(action, context);
      return {
        name: 'ActionGroup',
        fn: (params: any, ...args: any[]) => doAction(subActions, context, params, args),
      };
    }

    const t = typeof action;

    if (t === 'function') {
      return {
        name: 'anonymous',
        fn: action,
      };
    }

    // recore-loader not support yet
    if (t === 'string' && typeof context[action] === 'function') {
      if (action in context) {
        return {
          name: action,
          fn: context[action].bind(context),
        };
      }
      return null;
    }

    // recore-loader not support yet
    if (action.name && typeof context[action.name] === 'function') {
      return {
        name: action.name,
        fn: context[action.name].bind(context),
        params: action.params,
      };
    }

    return null;
  }).filter(Boolean);
}

function doAction(queue: any[], context: any, params: any, args: any[]) {
  if (!queue) {
    return;
  }

  queue = queue.slice(0);

  let _resolve: () => void;

  const i = new Promise((resolve) => {
    _resolve = resolve;
  });

  const fail = (e: any) => {
    if (process.env.NODE_ENV === 'production') {
      reportError(e);
    } else {
      console.error(e); // tslint:disable-line
    }
  };

  const next = (previousResult?: any) => {
    const item = queue.shift();
    if (!item) {
      return _resolve();
    }

    let res;
    try {
      res = item.fn.apply(context, [{
        ...params,
        previousResult,
        ...item.params,
      }].concat(args));
    } catch (e) {
      return fail(e);
    }

    if (res === false) {
      // interrupt
      return null;
    }

    if (res && res.then) {
      res.then(next).catch(fail);
    } else if (res instanceof Error) {
      fail(res);
    } else {
      next(res);
    }
    return null;
  };

  next();

  return i;
}



