import {
  History,
  HashHistoryBuildOptions,
  BrowserHistoryBuildOptions,
  createBrowserHistory,
  createHashHistory,
  LocationState,
} from 'history';

export type HistoryMode = 'browser' | 'hash';

export type HistoryOptions = HistoryMode | ({
  mode?: HistoryMode;
} & (HashHistoryBuildOptions | BrowserHistoryBuildOptions));

export class Navigator {
  history: History | null = null;

  init(options: HistoryOptions = {}) {
    if (!this.history) {
      if (typeof options === 'string') {
        options = { mode: options };
      }
      if (options.mode === 'hash') {
        this.history = createHashHistory(options);
      } else {
        this.history = createBrowserHistory(options);
      }
    }
  }

  getHistory(): History {
    return this.history!;
  }

  goto(path: string, state?: LocationState): void {
    if (this.history) {
      this.history.push(path, state);
    }
  }

  goBack(): void {
    if (this.history) {
      this.history.goBack();
    }
  }
}

export default new Navigator();



