import {
  DerivationState,
  IDerivation,
  runDerivedFunction,
  isCaughtException,
  shouldCompute,
  clearObserving,
} from './derivation';
import { nextTick } from './next-tick';
import { IObservable, endBatch, startBatch } from './observable/observable';
import { globalState } from './global-state';
import { nextId, Lambda, throttle } from './utils';

export class Reaction implements IDerivation {
  observing: IObservable[] = [];
  dependenciesState = DerivationState.NOT_TRACKING;
  id = nextId();
  scheduled = false;
  scheduleRun: () => void;

  private sleeping = false;
  private running = false;
  private firstRun = true;

  constructor(
    public name: string,
    private action: Action,
    public priority: number = 0,
    throttleWait: number = 10
  ) {
    if (throttleWait > 0) {
      this.scheduleRun = throttle(this.run.bind(this), throttleWait);
    } else {
      this.scheduleRun = this.run.bind(this);
    }
  }

  onBecomeDirty() {
    this.schedule();
  }

  schedule() {
    if (this.scheduled || this.sleeping) {
      return;
    }
    this.scheduled = true;
    scheduleReaction(this);
  }

  isScheduled() {
    return this.scheduled;
  }

  isDirty(): boolean {
    return shouldCompute(this);
  }

  run() {
    // TODO warning this.running
    if (this.sleeping || this.running) {
      return;
    }

    startBatch();
    if (shouldCompute(this)) {
      this.running = true;
      const result = runDerivedFunction(this, this.action, {
        dispose: () => this.sleep(),
        firstRun: this.firstRun,
      });
      this.firstRun = false;
      this.running = false;
      // if (isCaughtException(result)) this.reportExceptionInDerivation(result.cause)
    }
    if (this.sleeping) {
      clearObserving(this);
    }
    endBatch();
  }

  sleep() {
    if (this.sleeping) {
      return;
    }
    this.sleeping = true;
    if (!this.running) {
      startBatch();
      clearObserving(this);
      endBatch();
      deScheduleReaction(this);
    }
  }

  wakeup() {
    this.sleeping = false;
  }
}

let flushIndex = 0;
let flushWaiting = false;

function scheduleReaction(reaction: Reaction) {
  const { pendingReactions, isRunningReactions } = globalState;
  if (!isRunningReactions) {
    pendingReactions.push(reaction);
  } else {
    let i = pendingReactions.length - 1;
    while (i > flushIndex && pendingReactions[i].priority < reaction.priority) {
      i--;
    }
    pendingReactions.splice(i + 1, 0, reaction);
  }

  runReactions();
}

function deScheduleReaction(reaction: Reaction) {
  const { pendingReactions, isRunningReactions } = globalState;
  if (!isRunningReactions) {
    const i = pendingReactions.indexOf(reaction);
    if (i > -1) {
      pendingReactions.splice(i, 1);
    }
  }
}

export function runReactions() {
  // queue the flush
  if (!flushWaiting) {
    flushWaiting = true;
    nextTick(flushReactions);
  }
}

const MAX_REACTION_ITERATIONS = 100;

function flushReactions() {
  globalState.isRunningReactions = true;
  const allReactions = globalState.pendingReactions;
  let pendingLength = 0;
  let iterations = 0;

  allReactions.sort((a, b) => b.priority - a.priority);

  while (allReactions.length > pendingLength) {
    pendingLength = allReactions.length;
    if (++iterations === MAX_REACTION_ITERATIONS) {
      // tslint:disable-next-line
      console.error(
        `Reaction doesn't converge to a stable state after ${MAX_REACTION_ITERATIONS} iterations.` +
        ` Probably there is a cycle in the reactive function: ${allReactions[0]}`
      );
      break;
    }
    for (; flushIndex < pendingLength; flushIndex++) {
      allReactions[flushIndex].scheduled = false;
      allReactions[flushIndex].scheduleRun();
    }
  }

  flushIndex = 0;
  flushWaiting = false;
  allReactions.length = 0;
  globalState.isRunningReactions = false;
}

export type Disposer = Lambda;
export interface AutorunOptions {
  throttle?: number;
  runFirstNow?: boolean;
}
export interface RunContext {
  dispose: Disposer;
  firstRun: boolean;
}
export type Action = (this: RunContext, context: RunContext) => any;

export function autorun(action: Action, options: number | AutorunOptions = {}): Disposer {
  const name: string = (action as any).name || "Autorun@" + nextId();
  if (typeof options === 'number') {
    options = { throttle: options };
  }

  const reaction = new Reaction(name, action, 0, options.throttle || 0);

  if (options.runFirstNow) {
    reaction.run();
  } else {
    reaction.schedule();
  }

  return () => {
    reaction.sleep();
  };
}



